.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header__logo {
  width: 40px;
  height: 40px;
}

.nav__list {
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  text-transform: uppercase;
  color: #060b35;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: flex;
}

.nav__link:hover {
  color: #00acdc;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: url("rect.632d5c09.png");
  height: 14px;
  position: absolute;
  top: 26px;
}

.main-container {
  box-sizing: border-box;
  justify-content: center;
  gap: 46px 48px;
  width: 100%;
  padding: 50px 40px;
  display: grid;
}

@media (width <= 488px) {
  .main-container {
    grid-template-columns: repeat(1, 200px);
  }
}

@media (width >= 488px) {
  .main-container {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .main-container {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .main-container {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  width: 200px;
  padding: 16px;
  transition: all .3s;
  display: flex;
}

.card__image {
  width: 160px;
  height: 134px;
  margin-top: 16px;
  margin-left: 3px;
  margin-right: 3px;
}

.card__title {
  color: #060b35;
  margin: 40px 0 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__product-code {
  color: #616070;
  align-self: start;
  margin: 4px 0 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__stars-container {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-top: 16px;
  display: flex;
}

.card__review {
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
}

.card__price-container {
  justify-content: space-between;
  align-items: flex-end;
  width: 100%;
  margin-top: 24px;
  display: flex;
}

.card__price {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__dollars {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  box-sizing: border-box;
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  display: flex;
}

.card__button--hover:hover {
  color: #00acdc;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card__title:hover {
  color: #34568b;
}

.card:hover {
  transform: scale(1.2);
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--4 > .stars__star:nth-last-child(n+2) {
  background-image: url("star-active.edda4c43.svg");
}

html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

body {
  margin: 0;
}

